@import "../../global.scss";

.topbar{
    width: 100%;
    height: 70px;
    background-color:#FFF4E0;
    box-shadow: 0px 0px 15px -8px black;
    color:$mainColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;
    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            display: flex;
            align-items: center;
            justify-content: center;
            .logo{
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color:inherit;
                margin-right: 40px;
                color:black;
                @include mobile{
                    display: none;
                }
               
            }
            .itemContainer{
               a {
                   text-decoration: none;
                   color: black;
               }
                @include mobile{
                    display: none;
                }
                display: flex;
                align-items: center;
                margin-left: 30px;
                .icon{
                    font-size: 18px;
                    margin-right: 5px;
                }
                span{
                    font-size: 15px;
                    font-weight: 500;
                }

            }
         }

         .right{
             .hamburger{
                 width: 32px;
                 height: 25px;
                 display: flex;
                 flex-direction: column;
                 justify-content: space-between;
                 cursor: pointer;
                 overflow: hidden;
                 span{
                     width: 100%;
                     height: 3px;
                     background-color: $mainColor;
                     transform-origin: left;
                     transition: all 2s ease;
                 }
             }
         }
     }

     &.active{
         background-color: #FFDB58;
         color:inherit;
        
         font-size: 30px;
         .hamburger{
           
            span{
                &:first-child{
                    background-color:black;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: black;
                    transform: rotate(-45deg);
                }
            }
         }
     }
}

